.department-name {
  position: absolute;
  top: calc(50% - 10.5px);
  left: calc(50% - 232.5px);
}
.department-name-parent {
  position: relative;
  border-radius: 6px;
  background-color: #285a87;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  width: 100%;
  height: 41px;
  text-align: left;
  font-size: 12px;
  color: #fff;
  font-family: Ruda;
}
