.b2,
.grade-12 {
  position: absolute;
  top: calc(50% - 10.5px);
  left: calc(50% - 235px);
}
.b2 {
  left: calc(50% + 174px);
}
.grade-12-parent {
  position: relative;
  border-radius: 6px;
  background-color: #285a87;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  width: 100%;
  height: 37px;
  text-align: left;
  font-size: 12px;
  color: #fff;
  font-family: Ruda;
}
