.frame-child51,
.frame-child52 {
  position: absolute;
  background-color: #fff;
}
.frame-child51 {
  top: 0;
  left: 0;
  border-radius: 6px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.25);
  width: 353px;
  height: 126px;
}
.frame-child52 {
  top: 8px;
  left: 8px;
  border: 1px solid #c4c4c4;
  box-sizing: border-box;
  width: 335px;
  height: 42px;
}
.mendatory,
.select-schedule-cayegory {
  position: absolute;
  top: 63px;
  left: 10px;
}
.mendatory {
  top: 98px;
}
.rectangle-root {
  position: relative;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  width: 353px;
  height: 126px;
  max-width: 100%;
  max-height: 100%;
  overflow: auto;
  text-align: left;
  font-size: 15px;
  color: #7a7a7a;
  font-family: "Segoe MDL2 Assets";
}
