.mail-icon {
  position: absolute;
  top: 5px;
  left: 11px;
  width: 24px;
  height: 24px;
  overflow: hidden;
}
.send-report-email {
  position: absolute;
  top: 0;
  left: 45px;
  line-height: 34px;
}
.mail-parent {
  position: relative;
  border-radius: 13px;
  background-color: #dedede;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  width: 100%;
  height: 34px;
  text-align: left;
  font-size: 12px;
  color: #7a7a7a;
  font-family: Arial;
}
