.frame-child53,
.frame-child54 {
  position: absolute;
  background-color: #fff;
}
.frame-child53 {
  top: 0;
  left: 0;
  border-radius: 6px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.25);
  width: 353px;
  height: 126px;
}
.frame-child54 {
  top: 8px;
  left: 8px;
  border: 1px solid #c4c4c4;
  box-sizing: border-box;
  width: 335px;
  height: 42px;
}
.default-academic-session,
.select-academic-session {
  position: absolute;
  top: 63px;
  left: 10px;
}
.default-academic-session {
  top: 98px;
}
.rectangle-top {
  position: relative;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  width: 100%;
  height: 126px;
  text-align: left;
  font-size: 15px;
  color: #7a7a7a;
  font-family: "Segoe MDL2 Assets";
}
