.div49 {
  position: absolute;
  top: calc(50% - 8px);
  left: calc(50% - 8.5px);
  letter-spacing: -0.03em;
  line-height: 22px;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 17px;
  height: 16px;
}
.parent6 {
  position: relative;
  border-radius: 4px;
  background-color: #3c8cd6;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  width: 19px;
  height: 18px;
  max-width: 100%;
  max-height: 100%;
  overflow: auto;
  text-align: center;
  font-size: 10px;
  color: #fff;
  font-family: Inter;
}
