.downloadprint-report {
  position: absolute;
  top: 0;
  left: 41px;
  line-height: 34px;
}
.download-icon {
  position: absolute;
  top: 5px;
  left: 9px;
  width: 20px;
  height: 20px;
  overflow: hidden;
}
.downloadprint-report-parent {
  position: relative;
  border-radius: 13px;
  background-color: #dedede;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  width: 100%;
  height: 34px;
  text-align: left;
  font-size: 12px;
  color: #7a7a7a;
  font-family: Arial;
}
