.vector-root {
  position: relative;
  border-radius: 39px;
  background-color: #fff;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  border: 1px solid rgba(122, 122, 122, 0.54);
  box-sizing: border-box;
  width: 250px;
  height: 148px;
  overflow: hidden;
  max-width: 100%;
  max-height: 100%;
  text-align: left;
  font-size: 12px;
  color: #7a7a7a;
  font-family: Arial;
}
